@font-face {
  font-family: BigJohnPRO;
  src: url(/static/media/BigJohnPRO-Regular.9b550f15.otf) format("opentype");
}

@font-face {
  font-family: BigJohnPRO;
  font-weight: bold;
  src: url(/static/media/BigJohnPRO-Bold.018838cb.otf) format("opentype");
}

@font-face {
  font-family: BigJohnPRO;
  font-weight: lighter;
  src: url(/static/media/BigJohnPRO-Light.90e40e07.otf) format("opentype");
}

@font-face {
  font-family: Monstserrat;
  src: url(/static/media/Montserrat-Medium.c8b6e083.ttf);
}

@font-face {
  font-family: Monstserrat;
  font-weight: bold;
  src: url(/static/media/Montserrat-Bold.ade91f47.ttf);
}

@font-face {
  font-family: Monstserrat;
  font-weight: bolder;
  src: url(/static/media/Montserrat-ExtraBold.19ba7aa5.ttf);
}

@font-face {
  font-family: Monstserrat;
  font-weight: lighter;
  src: url(/static/media/Montserrat-ExtraBold.19ba7aa5.ttf);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  overflow-x: hidden;
  width: 100vw;
}
body {
  overflow-x: hidden;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}
/* html{
    
    font-family: 'Montserrat', sans-serif;
} */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.magic,
.magic div,
.magic a,
.magic svg,
.magic path,
.magic .decor,
.magic *,
.magic h1 .mauapa {
  cursor: url(data:image/png;base64,), auto;
}

